import React from 'react';
import { graphql } from 'gatsby';
import Layout from '../shared/Layout';
import SecondaryPageWrapper from '../styled/SecondaryPageWrapper';
import Banner from '../product/Banner';
import Section1 from '../product/Section1';
import Section2 from '../product/Section2';
import Section3 from '../product/Section3';
import Section4 from '../product/Section4';

const ProductTemplate = ({ data }) => {
  const {
    wordpressWpProducts: { acf },
  } = data;
  return (
    <Layout theme="secondary">
      <SecondaryPageWrapper>
        <Banner
          image={acf.banner_image}
          title={acf.banner_title}
          copy={acf.banner_copy}
        />
        <Section1 features={acf.section_1_features} />
        <Section2 heading={acf.section_2_heading} copy={acf.section_2_copy} />
        <Section3 features={acf.section_3_features} />
        <Section4
          heading={acf.section_4_heading}
          copy={acf.section_4_copy}
          cta={acf.section_4_cta}
          url={acf.section_4_cta_url}
          image={acf.section_4_image}
        />
      </SecondaryPageWrapper>
    </Layout>
  );
};

export default ProductTemplate;

export const query = graphql`
  query productQuery($id: Int) {
    wordpressWpProducts(wordpress_id: { eq: $id }) {
      acf {
        banner_title
        banner_copy
        banner_image {
          localFile {
            childImageSharp {
              fluid(maxWidth: 1920) {
                ...GatsbyImageSharpFluid_withWebp
              }
            }
          }
        }
        section_1_features {
          heading
          copy
        }
        section_2_heading
        section_2_copy
        section_3_features {
          heading
          copy
        }
        section_4_heading
        section_4_copy
        section_4_cta
        section_4_cta_url
        section_4_image {
          localFile {
            childImageSharp {
              fluid(maxWidth: 1920) {
                ...GatsbyImageSharpFluid_withWebp
              }
            }
          }
        }
      }
    }
  }
`;
