import React from 'react';
import { css } from '@emotion/core';
import Container from '../styled/Container';
import { BodyText, SectionHeading } from '../styled/Typography';

const Section2 = ({ heading, copy }) => {
  return (
    <section css={baseCSS}>
      <Container theme="secondary">
        <div css={contentCSS}>
          <SectionHeading style={sectionHeadingCSS}>{heading}</SectionHeading>
          <BodyText theme="large">{copy}</BodyText>
        </div>
      </Container>
    </section>
  );
};

export default Section2;

const baseCSS = css`
  background-color: #f8f8f8;
  padding: 3.125rem 0 4.375rem;
`;

const sectionHeadingCSS = css`
  margin-bottom: 1.125rem;
  text-align: center;
`;

const contentCSS = css`
  margin: 0 auto;
  width: 80%;
`;
