import React from 'react';
import { css } from '@emotion/core';
import Container from '../styled/Container';
import BackgroundImage from '../styled/BackgroundImage';
import { BodyText, SectionHeading } from '../styled/Typography';
import Button from '../styled/Button';

const Section4 = ({ heading, copy, cta, url, image }) => {
  return (
    <section css={baseCSS}>
      <BackgroundImage image={image} style={imageCSS} />
      <Container theme="secondary">
        <SectionHeading style={sectionHeadingCSS}>{heading}</SectionHeading>
        <BodyText style={bodyTextCSS} theme="large">
          {copy}
        </BodyText>
        <Button theme="secondary" url={url} style={buttonCSS}>
          {cta}
        </Button>
      </Container>
    </section>
  );
};

export default Section4;

const baseCSS = css`
  align-items: center;
  display: flex;
  min-height: 20.25rem;
  padding: 3.125rem 0;
  position: relative;
`;

const imageCSS = css`
  bottom: 0;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  z-index: -1;

  &:after {
    background: rgba(0, 0, 0, 0.8);
    bottom: 0;
    content: '';
    left: 0;
    position: absolute;
    right: 0;
    top: 0;
    z-index: 0;
  }
`;

const sectionHeadingCSS = css`
  color: #fff;
  margin-bottom: 1.125rem;
`;

const bodyTextCSS = css`
  color: #fff;
`;

const buttonCSS = css`
  margin: 0;
`;
