import React from 'react';
import { css } from '@emotion/core';
import Container from '../styled/Container';
import { CopyHeading, BodyText } from '../styled/Typography';
import { viewports } from '../../utils/variables';

const Section3 = ({ features }) => {
  return (
    <section css={baseCSS}>
      <Container theme="secondary">
        <div css={featuresCSS}>
          {features.map(feature => (
            <div css={featureCSS} key={feature.heading}>
              <CopyHeading theme="large">{feature.heading}</CopyHeading>
              <div css={dividerCSS} />
              <BodyText theme="large">{feature.copy}</BodyText>
            </div>
          ))}
        </div>
      </Container>
    </section>
  );
};

export default Section3;

const baseCSS = css`
  padding: 4.375rem 0 5.625rem;
`;

const featuresCSS = css`
  align-items: flex-start;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  margin: 0 auto 2.5rem;

  @media ${viewports.medium} {
    width: 80%;
  }
`;

const featureCSS = css`
  padding: 0 1.125rem;
  text-align: left;

  @media ${viewports.medium} {
    width: 50%;
  }
`;

const dividerCSS = css`
  background-color: #d8d8d8;
  height: 1px;
  margin: 1.875rem 0 2.125rem;
  width: 100%;
`;
