import React from 'react';
import { css } from '@emotion/core';
import BackgroundImage from '../styled/BackgroundImage';
import Container from '../styled/Container';
import { SectionHeading, BodyText } from '../styled/Typography';
import { colors, styles, viewports } from '../../utils/variables';

const Banner = ({ image, title, copy }) => {
  return (
    <>
      <section css={baseCSS}>
        <BackgroundImage image={image} style={imageCSS} />
      </section>
      <Container>
        <div css={innerContainerCSS}>
          <SectionHeading style={sectionHeadingCSS}>{title}</SectionHeading>
          <BodyText>{copy}</BodyText>
        </div>
      </Container>
    </>
  );
};

export default Banner;

const baseCSS = css`
  align-items: flex-end;
  display: flex;
  height: 35vh;
  position: relative;
  z-index: 0;
`;

const imageCSS = css`
  height: 100%;
  position: absolute;
  width: 100%;
  z-index: 0;

  &:before {
    background-color: rgba(0, 0, 0, 0.15);
    bottom: 0;
    content: '';
    left: 0;
    position: absolute;
    right: 0;
    top: 0;
    z-index: 1;
  }
`;

const innerContainerCSS = css`
  background: #fff;
  border: 2px ${colors.black} solid;
  box-shadow: ${styles.boxShadow};
  margin: 0 auto;
  padding: 1.25rem;
  position: relative;
  transform: translateY(-4rem);
  z-index: 1;

  @media ${viewports.medium} {
    padding: 1.25rem 6.25rem;
    width: 66.67%;
  }
`;

const sectionHeadingCSS = css`
  font-size: 1.75rem;
  margin-bottom: 1rem;
  text-align: center;

  @media ${viewports.medium} {
    font-size: 2.25rem;
  }
`;
