import React from 'react';
import { css } from '@emotion/core';
import Container from '../styled/Container';
import { CopyHeading, BodyText } from '../styled/Typography';
import { colors, viewports } from '../../utils/variables';

const Section1 = ({ features }) => {
  return (
    <section css={baseCSS}>
      <Container theme="secondary">
        <div css={featuresCSS}>
          {features.map(feature => (
            <div css={featureCSS} key={feature.heading}>
              <div css={iconCSS} />
              <CopyHeading>{feature.heading}</CopyHeading>
              <BodyText theme="large">{feature.copy}</BodyText>
            </div>
          ))}
        </div>
      </Container>
    </section>
  );
};

export default Section1;

const baseCSS = css`
  padding: 0.375rem 0 3.125rem;
`;

const featuresCSS = css`
  align-items: flex-start;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  margin: 0 -1.125rem 2.5rem;
`;

const featureCSS = css`
  padding: 0 1.125rem;
  text-align: left;

  @media ${viewports.medium} {
    width: 33.3333%;
  }
`;

const iconCSS = css`
  background-color: ${colors.gray};
  border-radius: 50%;
  height: 2.5rem;
  margin-bottom: 0.875rem;
  width: 2.5rem;
`;
